<template>
  <page @on-ready="onReady" :headerHeightVW="headerHeightVW" ref="page" class="tonghao-login-page">
    <div class="login-top">
      <img class="login-logo" src="../../assets/images/login/logo.png" />

      <div class="login-input">
        <van-field
          v-model="login.account"
          placeholder="录入帐号"
          :border="false"
          class="field-cell"
          clearable
          ref="accountField"
        ></van-field>

        <van-field
          v-model="login.password"
          placeholder="录入密码"
          :border="false"
          class="field-cell"
          type="password"
          clearable
        ></van-field>
      </div>
    </div>
    <div class="login-action">
      <a href="javascript:;" @click="handleLogin">登录</a>
    </div>
  </page>
</template>

<script>
import Page from "../../components/Page.vue";
import navigate from "../../utils/navigate.js";
import io from "../../api";

export default {
  components: {
    Page
  },
  data() {
    return {
      login: {
        account: "",
        password: ""
      },
      headerHeightVW: 0,
      winHeight: ""
    };
  },
  mounted() {},
  methods: {
    onReady() {},
    onForgetClick() {
      navigate.open("retrieve");
    },
    handleLogin() {
      this.$toast.loading({
        loadingType: "spinner",
        duration: 0
      });

      io.login({ ...this.login, deviceId: localStorage.getItem("deviceToken") })
        .then(data => {
          try {
            const ajpush = api.require("ajpush");
            ajpush.bindAliasAndTags(
              {
                alias: localStorage.getItem("deviceToken"),
                tags: ['user' + data.user.id + '']
                // tags: [localStorage.getItem("deviceToken") + ''],
              },
              (ret, err) => {}
            );
          } catch {
          } finally {
            window.localStorage.setItem("token", data.token);
            window.localStorage.setItem("user", JSON.stringify(data.user));
            this.$toast.clear();
            navigate.back();
          }
        })
        .catch(msg => {
          this.$toast.clear();
          this.$toast(msg);
        });
    }
  }
};
</script>

